
import { defineComponent } from "@vue/composition-api";
import mockData from "../../services/api/mockData";

export default defineComponent({
  name: "Index",
  props:{
    item: {}
  },
  data() {
    return {
      showNavBottom: false,
      items: mockData.rateTable,
    };
  },
  methods: {
    nextStep() {
      if (this.handleCalculate() === true) {
        this.$router.push("/loan");
      }
    },
  },
});
